




























import { Component, Vue } from "vue-property-decorator";
@Component({ components: {} })
export default class HeaderTop extends Vue {
  goUserCenter() {
    this.$router.push({
      path: "/my-center"
    });
  }
  handleCommand(e: any) {
    if (e === 'cn') {
      this.$i18n.locale = "zh"; //重新设置
    } else {
      this.$i18n.locale = 'en'; //重新设置
    }
  }
}
